@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;1,100;1,200;1,300&display=swap);
:root {
  --primary: #7cc3c1;
  --white: #f5f5f5;
  --secondary: #f7f8fb;
  --danger: rgba(255, 0, 0, 0.683);
  --grey: #767676;
  --dashboardColor: #eeeeee;
  --deletedColor: #ff675f;
}
* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
}
body {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.tableLinks {
  color: var(--primary) !important;
  text-decoration: none !important;
  font-weight: bolder;
}

/* overwriting the pagination default styling start here */
.page-link {
  color: var(--primary) !important;
  font-weight: bolder;
}
.page-item.active .page-link {
  z-index: 3;
  color: #fff !important;
  background-color: var(--primary) !important;
  border: none !important;
}

.page-link:focus {
  box-shadow: none !important;
}
/* end here */

/* overwriting sweet alert starts here */
.react-confirm-alert-body > h1 {
  font-size: 20px !important;
  margin-bottom: 30px;
}

.react-confirm-alert-button-group button:nth-child(1) {
  background-color: var(--primary);
}

.react-confirm-alert-button-group button:nth-child(2) {
  background-color: red;
}

/* ends here */

a {
  color: var(--primary) !important  ;
}

